import React, { useEffect, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const RtcResults = () => {
  const [idCode, setIdCode] = useState({});
  const [sociodemographicData, setsociodemographicData] = useState({});
  const [rtcData, setRtcData] = useState({});
  const [totalRtcData, setTotalRtcData] = useState({});

  useEffect(() => {
    const savedIdCode = localStorage.getItem("idCode");
    const savedSociodemographicData = localStorage.getItem("sociodemographicData");
    const savedRtcData = localStorage.getItem("rtcData");
    const savedTotalRtcData = localStorage.getItem("totalRtcData");

    if (savedIdCode) {
      setIdCode(savedIdCode);
    }

    if (savedSociodemographicData) {
      const parsedsociodemographicData = JSON.parse(savedSociodemographicData);
      setsociodemographicData(parsedsociodemographicData);
    }

    if (savedRtcData) {
      const parsedAnswers = JSON.parse(savedRtcData);

      // Remove "REVERSED" from the key
      const updatedAnswers = {};
      Object.keys(parsedAnswers).forEach((key) => {
        const updatedKey = key.replace("_REVERSED", "");
        updatedAnswers[updatedKey] = parsedAnswers[key];
      });

      setRtcData(updatedAnswers);
    }

    if (savedTotalRtcData) {
      const parsedTotalRtcData = JSON.parse(savedTotalRtcData);
      setTotalRtcData(parsedTotalRtcData);
    }
  }, []);

  const downloadLocalStorageAsJSON = () => {
    const jsonData = JSON.stringify(
      {
        idCode,
        sociodemographicData,
        rtcData,
      },
      null,
      2
    ); // Convert sociodemographicData and answers objects to JSON string with indentation

    const element = document.createElement("a");
    const file = new Blob([jsonData], { type: "application/json" });

    element.href = URL.createObjectURL(file);
    element.download = `${idCode}_rtc.json`;
    document.body.appendChild(element); // Required for Firefox
    element.click();
    document.body.removeChild(element); // Clean up the element
  };

const rtcDimension =
    {
      rtcImp: { tot: totalRtcData.rtcImpTot, mean: 16.24, ds: 2.464, name: 'Importanza percepita problema/cambiamento'},
      rtcMot: { tot: totalRtcData.rtcMotTot, mean: 15.26, ds: 2.715, name: 'Motivazione al cambiamento'},
      rtcAue: { tot: totalRtcData.rtcAueTot, mean: 18.3, ds: 3.117, name: 'Autoefficacia'},
      rtcEff: { tot: totalRtcData.rtcEffTot, mean: 14.46, ds: 2.493, name: 'Efficacia soluzione proposta'},
      rtcSoc: { tot: totalRtcData.rtcSocTot, mean: 13.36, ds: 2.629, name: 'Supporto sociale'},
      rtcAct: { tot: totalRtcData.rtcActTot, mean: 14.84, ds: 2.845, name: 'Azione'},
      rtcRea: { tot: totalRtcData.rtcReaTot, mean: 15.18, ds: 2.644, name: 'Readiness percepita'}
    }

let veryLow = []
let low = []
let medium = []
let high = []

// Iterate through the keys of rtcData and access the 'mean' and 'Ds' values
Object.entries(rtcDimension).map(([key, value]) => {
  const minusTwoDs = (value.mean - (value.ds * 2)).toFixed(3)
  const minusOneDs = (value.mean - value.ds).toFixed(3)
  const plusOneDs = (value.mean + value.ds).toFixed(3)
  // const plusTwoDs = (value.mean + (value.ds * 2)).toFixed(3)


  if (value.tot <= minusTwoDs) {
    veryLow.push(value.name);
  } else if (value.tot > minusTwoDs && value.tot <= minusOneDs) {
    low.push(value.name);
  } else if (value.tot > minusOneDs && value.tot <= plusOneDs) {
    medium.push(value.name);
  } else if (value.tot >= plusOneDs) {
    high.push(value.name);
  }
  return key
});


const chartData = {
  labels: Object.keys(rtcDimension).map((key) => rtcDimension[key].name),
  datasets: [
    {
      label: "Media (?)",
      backgroundColor: "rgba(56, 23, 102, 0.5)",
      borderColor: "rgba(56, 23, 102, 1)",
      // borderWidth: 1,
      data: Object.keys(rtcDimension).map((key) => rtcDimension[key].mean),
    },
    {
      label: "Tuo punteggio",
      backgroundColor: "rgba(88, 200, 132, 0.5)",
      borderColor: "rgba(88, 200, 132, 1)",
      // borderWidth: 1,
      data: Object.keys(rtcDimension).map((key) => rtcDimension[key].tot),
    },
  ],
};

const chartOptions = {
  indexAxis: 'y', // Set the index axis to y for horizontal chart
  scales: {
    x: {
      type: "linear", // Use "linear" scale for the x-axis
      beginAtZero: true,
    },
  },
};

  return (
    <div className="w-full py-[2rem] px-4 bg-white">
      <div className="max-w-[800px] w-full mx-auto text-center flex flex-col">
        <button
          className="bg-black text-[#e3e3e3]  w-[200px] rounded-md font-medium my-6 mx-auto px-6 py-3"
          onClick={downloadLocalStorageAsJSON}
        >
          Download
        </button>

        <Bar data={chartData} options={chartOptions} />

        {veryLow.length > 0 && (
          <div className="border-2 border-red-400 shadow-md flex flex-col p-4 my-4 mb-6 rounded-lg">
            <div className="border-b-2 w-full border-red-400">
              <h2>Molto basso</h2>
            </div>
            <p className="text-left">Per quanto riguarda:</p>
            <ul className="text-left list-outside list-disc ml-6">
              {veryLow.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
            <p className="text-left">non sembri sentirti affatto pronto/a</p>
          </div>
        )}

        {low.length > 0 && (
          <div className="border-2 border-orange-400 shadow-md flex flex-col p-4 my-4 mb-6 rounded-lg">
            <div className="border-b-2 w-full border-orange-400">
              <h2>Basso</h2>
            </div>
            <p className="text-left">In termini di</p>
            <ul className="text-left list-outside list-disc ml-6">
              {low.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
            <p className="text-left">ci sono possibilit di miglioramento. Il punteggio  un po' basso, ma puoi migliorare. </p>
          </div>
        )}

        {medium.length > 0 && (
          <div className="border-2 border-green-400 shadow-md flex flex-col p-4 my-4 mb-6 rounded-lg">
            <div className="border-b-2 w-full border-green-400">
              <h2>Medio</h2>
            </div>
            <p className="text-left">I tuoi punteggi di </p>
            <ul className="text-left list-outside list-disc ml-6">
              {medium.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
            <p className="text-left">appaiono essere in media con quegli degli altri. Questo non  male considerando come le persone, comunque in media, si sentano abbastanza pronte a fronteggiare il cambiamento.</p>
          </div>
        )}

        {high.length > 0 && (
          <div className="border-2 border-green-600 shadow-md flex flex-col p-4 my-4 mb-6 rounded-lg">
            <div className="border-b-2 w-full border-green-600">
              <h2>Alto</h2>
            </div>
            <p className="text-left">I tuoi livelli di </p>
            <ul className="text-left list-outside list-disc ml-6">
              {high.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
            <p className="text-left"> sono ottimi. Complimenti. Mi raccomando per, non crogiolarti. Continua cos.</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default RtcResults;
