import React, { useState, useEffect } from "react";
import Firenze from "../assets/firenze_outlin3.png";
import axios from "axios";

const Mapp = () => {
  const [sociodemographicResults, setSociodemographicResults] = useState([]);
  const [rtcResults, setRtcResults] = useState([]);

  useEffect(() => {
    const fetchAllResults = async () => {
      try {
        const sociodemographicRes = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/results/sociodemographic`);
        setSociodemographicResults(sociodemographicRes.data);
        console.log(sociodemographicRes.data)

        const rtcRes = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/results/rtc`);
        setRtcResults(rtcRes.data);
        console.log(rtcRes.data)

      } catch (err) {
        console.log(err);
      }
    };

    fetchAllResults();
  }, []);



  // function calculateRTCEntrySum(rtcEntry) {
  //   let sum = 0;
  //   for (let i = 1; i <= 29; i++) {
  //     sum += parseInt(rtcEntry[`rtc_${i}`]);
  //   }
  //   return sum;
  // }

  function calculateRTCEntryMean(rtcEntry) {
    let sum = 0;
    for (let i = 1; i <= 29; i++) {
      sum += parseInt(rtcEntry[`rtc_${i}`]);
    }

    const numberOfAnswers = 29; // Assuming there are 29 questions in RTC
    return sum / numberOfAnswers;
  }


  // const districts = ["campo_di_marte", "centro", "rifredi", "isolotto", "gtavina"];
  // const districtSums = {};

  // districts.forEach((district) => {
  //   const rtcEntriesForDistrict = rtcResults.filter((rtcEntry) => {
  //     const sociodemographicEntry = sociodemographicResults.find((sociodemographic) => sociodemographic.idCode === rtcEntry.idCode);
  //     return sociodemographicEntry && sociodemographicEntry.district === district;
  //   });

  //   const districtSum = rtcEntriesForDistrict.reduce((sum, rtcEntry) => {
  //     return sum + calculateRTCEntryMean(rtcEntry);
  //   }, 0);

  //   districtSums[district] = districtSum;
  // });


  const districts = ["campo_di_marte", "centro", "rifredi", "isolotto", "gavinana"];
  const districtData = {};

districts.forEach((district) => {
  const rtcEntriesForDistrict = rtcResults.filter((rtcEntry) => {
    const sociodemographicEntry = sociodemographicResults.find((sociodemographic) => sociodemographic.idCode === rtcEntry.idCode);
    return sociodemographicEntry && sociodemographicEntry.district === district;
  });

  const numberOfUsers = rtcEntriesForDistrict.length;

  const districtSum = rtcEntriesForDistrict.reduce((sum, rtcEntry) => {
    return sum + calculateRTCEntryMean(rtcEntry);
  }, 0);

  const districtMean = districtSum / numberOfUsers;

  districtData[district] = {
    numberOfUsers,
    mean: districtMean,
  };
});


  console.log(districtData);
  return (
    <div className="text-white">
      <div className="max-w-[800px] mt-[-96px] w-full h-screen mx-auto text-center flex flex-col justify-center relative">
        <img src={Firenze} alt="Firenze Outline" />

        <div
          style={{
            position: "absolute",
            top: "9.85%",
            left: "7.4%",
            width: "10%",
            height: "5%",
            borderBottom: "2px solid #1a1a1a",
          }}
        >
          <p className="text-black">{districtData.rifredi.mean} Rifredi</p>
        </div>
        <div
          style={{
            position: "absolute",
            top: "72%",
            left: "7.4%",
            width: "10%",
            height: "5%",
            borderBottom: "2px solid #1a1a1a",
          }}
        >
          <p className="text-black">{districtData.isolotto.mean}Isolotto</p>
        </div>

        <div
          style={{
            position: "absolute",
            top: "82%",
            left: "45.2%",
            width: "10%",
            height: "5%",
            borderBottom: "2px solid #1a1a1a",
          }}
        >
          <p className="text-black">{districtData.centro.mean}Centro</p>
        </div>


        <div
          style={{
            position: "absolute",
            top: "82%",
            left: "72.3%",
            width: "10%",
            height: "5%",
            borderBottom: "2px solid #1a1a1a",
          }}
        >
          <p className="text-black">{districtData.gavinana.mean}Gavinana</p>
        </div>


        <div
          style={{
            position: "absolute",
            top: "25%",
            left: "83.1%",
            width: "10%",
            height: "5%",
            borderBottom: "2px solid #1a1a1a",
          }}
        >
          <p className="text-black">{districtData.campo_di_marte.mean}Campo di Marte</p>
        </div>

      </div>
      <p className="text-black">{districtData.mean}</p>
    </div>
  );
};

export default Mapp;
