import { React} from "react";
import Hero from "./components/Hero";
import Navbar from "./components/Navbar";
import Newsletter from "./components/Newsletter";

import { createBrowserRouter, RouterProvider, Outlet } from "react-router-dom";

import Rtc from "./pages/Rtc";
import Map from "./pages/Map";
import Instructions from "./pages/Instructions";
import RtcResults from "./pages/RtcResults";
import Sociodemographic from "./pages/Sociodemographic";


const Layout = () => {
  return (
    <div>
      {/* <Navbar /> */}
      <Outlet />
      {/* <Footer /> */}
    </div>
  );
};

const HomeLayout = () => {
  return (
    <div>
      <Hero />
    </div>
  );
};

const RtcResultLayout = () => {
  return (
    <div>
      <RtcResults />
      <Newsletter />
    </div>
  );
};

const MapLayout = () => {
  return (
    <div>
      {/* <Mapp /> */}
      <Map />
      {/* <Analytics />
      <Newsletter />
      <Cards /> */}
    </div>
  );
};


const router = createBrowserRouter([
  {
    path: "/",
    element: <Layout />,
    children: [
      {
        path: "/",
        element: <HomeLayout />,
      },
      {
        path: "/rtc",
        element: <Rtc />,
      },
      {
        path: "/map",
        element: <MapLayout />,
      },
      {
        path: "/instructions",
        element: <Instructions />,
      },
      {
        path: "/results",
        element: <RtcResultLayout />,
      },
      {
        path: "/sociodemographic",
        element: <Sociodemographic />,
      },
    ],
  },
]);

function App() {
  return (
    <div>
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
