import React from "react";
import { Link } from "react-router-dom";
import Video from "../assets/video.mp4";

const Hero = () => {
  return (
    <div className="text-white relative">
      <video
        autoPlay
        loop
        muted
        id="video"
        className="object-cover w-full h-screen absolute top-0 left-0"
      >
        <source src={Video} />
      </video>
      <div className="max-w-[800px] h-screen mx-auto text-center flex flex-col justify-center relative z-10">
        <p className="text-[#009999] font-bold p-2">Partecipa [...]</p>
        <h1 className="md:text-7xl sm:text-6xl text-4xl font-bold md:py-6">
          PHOENIX
        </h1>
        <div className="flex justify-center items-center">
          <p className="md:text-5xl sm:text-4xl text-xl font-bold py-4">
            The Rise of Citizen Voices for a Greener Europe
          </p>
        </div>
        <p className="md:text-2xl text-xl font-bold text-gray-500">Partecipa</p>
        <Link to="/instructions">
          <button className="bg-[#009999] text-[#fffdfa] w-[200px] rounded-md font-medium my-6 mx-auto py-3">
            Inizia
          </button>
        </Link>
      </div>
    </div>
  );
};

export default Hero;
