import React, { useState } from "react";
import { Link } from "react-router-dom";

const Instructions = () => {
  // Use state to control the visibility of the "Thank you anyway" text
  const [showThankYou, setShowThankYou] = useState(false);

  // Function to handle the "Rifiuta" button click
  const handleDisagreeClick = () => {
    // Hide the instructions and show the "Thank you anyway" text
    setShowThankYou(true);
  };

  return (
    <div className="max-w-[800px] w-full mx-auto text-center flex flex-col justify-center">
      <h1 className="w-full text-3xl font-bold text-[#009999] m-4">
        Istruzioni
      </h1>
      {/* Conditional rendering for instructions text */}
      {showThankYou ? (
        <p className="text-2xl text-[#009999] font-bold mt-6">
          Thank you anyway!
        </p>
      ) : (
        <p className="md:text-2xl text-xl text-justify  my-6 mx-auto py-3 text-gray-500">
      Gentile partecipante,
      chiediamo la tua disponibilit a prendere
      parte a un progetto di ricerca del VirtHuLab, dell'Universit degli
      studi di Firenze, Dipartimento di Formazione, Lingue, Intercultura,
      Letterature e Psicologia. L' attivit prevista sar la
      somministrazione di un questionario. Saranno sempre garantiti la tua privacy e il
      tuo anonimato. I dati, collettivamente raccolti, saranno soggetti ad
      elaborazione statistica solo in forma aggregata e anonima. In altri
      termini non sar in nessun caso possibile risalire all'identit del
      rispondente allo studio, una volta che la risposta  stata inviata o
      consegnata. I dati personali verranno conservati in un apposito
      archivio solo per il tempo necessario al conseguimento delle
      finalit per le quali sono stati raccolti o per qualsiasi altra
      legittima finalit ad essi collegata. Il periodo di conservazione
      dei dati non superer in ogni caso i 2 anni. Il Trattamento e la
      protezione dei dati personali sono regolamentati ai sensi del
      D.Lgs.n.101/18. e ai sensi del Regolamento UE 2016/679. Si precisa
      che - come da Decreti del Direttore Generale n.       2003/2017 e
      580/2018 - il Titolare del trattamento  l'Universit degli Studi di
      Firenze e si individua come Referente per la protezione dei dati, la
      direttrice del Dipartimento di Formazione, Lingue, Intercultura,
      Letterature e Psicologia (FORLILPSI) Vanna Boffo
      (vanna.boffo@unifi.it). Per qualsiasi dubbio  possibile
      rivolgersi al Dr. Andrea Guazzini (andrea.guazzini@unifi.it).
      Ti ricordiamo che la tua partecipazione a questa ricerca  completamente volontaria e sarai libero/a di ritirati in qualsiasi momento.
      Cliccando Acconsento dichiari di: (1) essere stato/a informato/a ai sensi del D.Lgs.n.101/18, e ai
      sensi del Regolamento UE 2016/679 sulla tutela delle persone e di
      altri soggetti rispetto al trattamento dei dati personali circa le
      modalit e le finalit del trattamento cui essi saranno sottoposti e
      circa la natura facoltativa del trasferimento di tali dati; (2) di acconsentire, ai sensi del D.Lgs.n.101/18 del Regolamento UE
      2016/679, a che tali dati vengano registrati e che, in forma
      anonima, vengano sottoposti ai promotori dello studio per essere
      esaminati, ai soli fini e nei limiti previsti dalla legge della
      citata Autorizzazione; (3) acconsentire inoltre che essi vengano analizzati ed archiviati in
      forma anonima e distrutti dopo al massimo 2 anni dalla raccolta.
        </p>
      )}
      {/* Conditionally render the buttons */}
      {!showThankYou ? (
        <>
          <Link to="/sociodemographic">
            <button className="bg-[#009999] text-[#fffdfa] w-[200px] rounded-md font-medium my-6 mx-auto py-3">
              Accetta
            </button>
          </Link>
          <button
            className="bg-[#009999] text-[#fffdfa] w-[200px] rounded-md font-medium my-6 mx-auto py-3"
            onClick={handleDisagreeClick}
          >
            Rifiuta
          </button>
        </>
      ) : null}
    </div>
  );
};

export default Instructions;
