import React, { useState } from "react";
import Questions from "./RtcQuestions.json";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Rtc = () => {
  const savedIdCode = localStorage.getItem("idCode");
  const navigate = useNavigate();

  const [answers, setAnswers] = useState({
    savedIdCode: savedIdCode,
  });

  console.log(savedIdCode)
  console.log(answers);

  const handleChange = (e) => {
    setAnswers((prev) => ({
      ...prev,
      [e.target.name.replace(/([a-z])([A-Z0-9])/g, "$1_$2").toUpperCase()]:
        e.target.value,
    }));
  };

  const saveAnswers = async () => {
    try {
      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/survey/rtc`,
        answers
      );
      calculateSum()
      navigate("/results");
    } catch (err) {
      console.log(err);
    }
  };

  const calculateSum = () => {
    const totalRtcData = {};

    let rtcImpTot = 0;
    let rtcMotTot = 0;
    let rtcAueTot = 0;
    let rtcEffTot = 0;
    let rtcSocTot = 0;
    let rtcActTot = 0;
    let rtcReaTot = 0;

    Object.entries(answers).forEach(([key, val]) => {
      if (key.includes("REVERSED")) {
        val = 6 - val;
      }
      // if (key.includes("FILLER")) {
      //   val = 0;
      // }
      if (key.startsWith("RTC_IMP_")) {
        rtcImpTot += parseInt(val);
      } else if (key.startsWith("RTC_MOT_")) {
        rtcMotTot += parseInt(val);
      } else if (key.startsWith("RTC_AUE_")) {
        rtcAueTot += parseInt(val);
      } else if (key.startsWith("RTC_EFF_")) {
        rtcEffTot += parseInt(val);
      } else if (key.startsWith("RTC_SOC_")) {
        rtcSocTot += parseInt(val);
      } else if (key.startsWith("RTC_ACT_")) {
        rtcActTot += parseInt(val);
      } else if (key.startsWith("RTC_REA_")) {
        rtcReaTot += parseInt(val);
      }
      // Store the key-value pair in the dictionary
    });

    // Save the totals to the dictionary
    totalRtcData["rtcImpTot"] = rtcImpTot;
    totalRtcData["rtcMotTot"] = rtcMotTot;
    totalRtcData["rtcAueTot"] = rtcAueTot;
    totalRtcData["rtcEffTot"] = rtcEffTot;
    totalRtcData["rtcSocTot"] = rtcSocTot;
    totalRtcData["rtcActTot"] = rtcActTot;
    totalRtcData["rtcReaTot"] = rtcReaTot;

    // Save the dictionary to local storage
    localStorage.setItem("totalRtcData", JSON.stringify(totalRtcData));

    // Save the values to local storage
    localStorage.setItem("rtcData", JSON.stringify(answers));
  };

  return (
    <div className="w-full py-[2rem] px-4 bg-white">
      <div className="max-w-[800px] w-full mx-auto text-center flex flex-col">
        <form>
          {Questions.map((question, idx) => (
            <div
              key={`question-no-${idx}`}
              className="border-2 border-gray-100 shadow-md flex flex-col p-4 my-4 mb-6 rounded-lg"
            >
              <h2 className="text-2xl font-bold text-left py-4">
                {idx + 1}. {question.questionText}
              </h2>

                <>
                  {question.answerOptions.map((option, optionIdx) => (
                    <div
                      key={`answer${idx}Option${optionIdx}`}
                      className="mt-3"
                    >
                      <label htmlFor={`answer${idx}Option${optionIdx}`}>
                        <input
                          type={question.answerType}
                          name={`${question.itemCodeName}${
                            question.reversedItem ? "_reversed" : ""
                          }${question.fillerItem ? "_filler" : ""}`}
                          id={`answer${idx}Option${optionIdx}`}
                          value={option.value}
                          className="hidden peer"
                          onChange={handleChange}
                        />
                        <div className="px-2 py-1 rounded-lg flex justify-left items-center font-medium text-black w-full sm:w-6/12 h-10 border-2 border-gray-200 peer-hover:border-[#009999] peer-checked:bg-[#009999] peer-checked:border-none peer-checked:text-white">
                          {option.answerText}{" "}
                        </div>
                      </label>
                    </div>
                  ))}
                </>

            </div>
          ))}
        </form>

        {/* full button on mobile */}

        <button
          className="bg-black text-[#e3e3e3]  w-[200px] rounded-md font-medium my-6 mx-auto px-6 py-3"
          onClick={saveAnswers}
        >
          Continua
        </button>

      </div>
    </div>
  );
};

export default Rtc;
