import React, { useState } from "react";
import Questions from "./surveys/Sociodemographic.json";
import { v4 as uuid } from "uuid";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const idCode = uuid();

const Sociodemographic = () => {
  // Initialize answers state with the same idCode value
  const [answers, setAnswers] = useState({
    idCode: idCode.slice(0, 8),
    age: "",
    gender: "",
    district: "",
  });

  const [ageError, setAgeError] = useState(false);
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    const currentQuestion = Questions.find(
      (question) => question.itemCodeName === name
    );

    if (
      currentQuestion.acceptedAnswer === "number" &&
      !Number.isNaN(Number(value))
    ) {
      setAnswers((prev) => ({
        ...prev,
        [name]: value,
      }));
      setAgeError(false);
    } else if (currentQuestion.acceptedAnswer !== "number") {
      setAnswers((prev) => ({
        ...prev,
        [name.replace(/([a-z])([A-Z0-9])/g, "$1_$2").toUpperCase()]: value,
      }));
    } else {
      setAgeError(true);
    }
  };

  const saveAnswers = async () => {
    localStorage.setItem("idCode", idCode.slice(0, 8));
    localStorage.setItem("sociodemographicData", JSON.stringify(answers));

    try {
      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/survey/sociodemographic`,
        answers
      );
      navigate("/rtc");
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div className="w-full py-[2rem] px-4 bg-white">
      <div className="max-w-[800px] mt-24 w-full mx-auto text-center flex flex-col justify-center">
        <h1 className="md:text-7xl text-[#009999] sm:text-6xl text-4xl font-bold md:py-6">
          Istruzioni
        </h1>
        <p className="md:text-2xl text-xl font-bold my-6 mx-auto py-3 text-gray-500">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua.
        </p>
      </div>
      <div className="max-w-[800px] w-full mx-auto text-center flex flex-col">
        <form>
          {Questions.map((question, idx) => (
            <div
              key={`question-no-${idx}`}
              className="border-2 border-gray-100 shadow-xl flex flex-col p-4 my-4 mb-6 rounded-lg"
            >
              <h2 className="text-2xl font-bold text-left py-4">
                {idx + 1}. {question.questionText}
              </h2>

              {question.answerType === "radio" ? (
                <>
                  {question.answerOptions.map((option, optionIdx) => (
                    <div
                      key={`answer${idx}Option${optionIdx}`}
                      className="mt-3"
                    >
                      <label htmlFor={`answer${idx}Option${optionIdx}`}>
                        <input
                          type={question.answerType}
                          name={question.itemCodeName}
                          id={`answer${idx}Option${optionIdx}`}
                          value={option.value}
                          className="hidden peer"
                          onChange={handleChange}
                        />
                        <div className="px-2 py-1 rounded-lg flex justify-left items-center font-medium text-black w-full sm:w-6/12 h-10 border-2 border-gray-200 peer-hover:border-[#009999] peer-checked:bg-[#009999] peer-checked:border-none peer-checked:text-white">
                          {option.answerText}
                        </div>
                      </label>
                    </div>
                  ))}
                </>
              ) : (
                <>
                  <input
                    name={question.itemCodeName}
                    type={question.answerType}
                    id={`answer${idx}`}
                    className={`p-2 border-2 border-gray-200 hover:border-[#009999] focus:border-[#009999] w-full sm:w-6/12 h-10 rounded-lg text-black ${
                      question.itemCodeName === "age" ? "number-only" : ""
                    }`}
                    onChange={handleChange}
                    value={answers[question.itemCodeName] || ""}
                  />
                  {question.acceptedAnswer === "number" && ageError && (
                    <p className="text-red-500 text-sm mt-1 text-left">
                      {question.acceptedAnswer.toUpperCase()} only.
                    </p>
                  )}
                </>
              )}
            </div>
          ))}
        </form>

          <button
            className="bg-black text-[#e3e3e3] w-[200px] rounded-md font-medium my-6 mx-auto px-6 py-3"
            onClick={saveAnswers}
          >
            Continua
          </button>
      </div>
    </div>
  );
};

export default Sociodemographic;
