import {useRef, React} from "react";
import emailjs from '@emailjs/browser';


const Newsletter = () => {

  const form = useRef()

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_p9xghhr', 'template_97qio2p', form.current, 'QXdy8KiQ0xBRZvcgf')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset()
  };

  return (
    <div className="w-full py-16 text-black px-4">
      <div className="max-w-[1240px] mx-auto grid lg:grid-cols-3">
        <div className="lg:col-span-2 my-4">
          <h1 className="md:text-4xl sm:text-3xl text-2xl font-bold py-2">
            Vuoi inviare il risultato?
          </h1>
          <p className="text-gray-500">
            Scrivi la tua email per ricevere i risultati
          </p>
        </div>

        <form ref={form} onSubmit={sendEmail}>
          <div className="my-4">
            <div className="flex flex-col sm:flex-row items-center justify-between w-full">
              <input
                className="p-3 flex w-full rounded-md text-black"
                type="email"
                name="email"
                placeholder="Enter Email"
                required
              />
              <button className="bg-[#009999] text-[#e3e3e3] rounded-md font-medium w-[200px] ml-4 my-6 px-6 py-3" type="submit">
                Invia
              </button>
            </div>
            {/* <p>
              We care bout the protection of your data. Read our{" "}
              <span className="text-[#009999]">Privacy Policy.</span>
            </p> */}
          </div>
        </form>
      </div>
    </div>
  );
};

export default Newsletter;
